ul li {
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  margin-bottom: 0.5rem; }

ul.default, ul.bullet {
  list-style-type: none;
  padding: 0;
  margin-top: 0.5rem; }

ul.bullet li {
  display: inline-block; }

ul.default li:before,
ul.bullet li:not(:last-child):after {
  font-family: "Font Awesome 5 Free";
  font-size: 0.8rem;
  font-weight: 900;
  content: "\F068"; }

ul.default li:before {
  margin-right: 0.5rem; }

ul.bullet li:after {
  margin-left: 0.5rem;
  margin-right: 0.5rem; }

ul.bullet li:first-child:before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\F5FC";
  margin-right: 0.5rem; }

